@mixin font {
  .roboto-regular {
    font-family: Roboto, sans-serif;
    font-weight: 400;
    font-style: normal;
  }

  .roboto-medium {
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-style: normal;
  }

  .roboto-bold {
    font-family: Roboto, sans-serif;
    font-weight: 700;
    font-style: normal;
  }
}
